function mapDispatchToProps(dispatch) {
  return {
    SetHeaderNavbar: (haveHeader) => {dispatch(ChangeNavbar(haveHeader))}
  }
}

function mapStateToProps(state) {
  return { }
}

export default connect (
  mapStateToProps,
  mapDispatchToProps
)(TermsAndPolicy);
